.search-empty {
    @include vertical;
    position: absolute;
    top: 153px;
    left: 0;
    width: 100%;
    height: calc(100% - 153px);
    &__container {
        display: inline-block;
        @include icenter; }
    &__title {
        font-size: 25px;
        color: $grey;
        margin-bottom: 42px; }
    img {
        max-width: 100%; } }

.people {
    margin-top: 36px;
    &__cards {
        @include flex-center;
        flex-wrap: wrap;
        margin: 0 -12px;
        align-items: stretch;
        @include md {
            margin: 0 -6px; } }
    &__card {
        flex: 0 0 33.33%;
        width: 33.33%;
        padding: 0 12px 30px;
        min-height: 192px;
        @include md {
            padding: 0 6px 18px; }
        @include sm {
            flex: 0 0 50%;
            width: 50%; }
        @include xs {
            flex: 0 0 100%;
            width: 100%; }
        p {
            padding-bottom: 12px;
            min-height: 64px;
            line-height: 1.231; }
        .card-outline {
            min-height: 193px; } } }

.search-results {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    padding-bottom: 25px;
    z-index: 100;
    background-color: rgba(#fff, 0.95);
    &:before,
    &:after {
        position: absolute;
        top: 0;
        bottom: 0;
        width: 300%;
        content: '';
        background-color: inherit;
        z-index: 100; }
    &:before {
        right: 100%; }
    &:after {
        left: 100%; }
    &__list {
        margin-top: 24px;
        margin-bottom: 12px; }
    &__link {
        font-size: 20px;
        font-weight: 700;
        line-height: 1.5; }
    &__highlighted {
        color: $grey; }
    &__title {
        font-size: 15px;
        line-height: 2;
        margin-bottom: 10px; }
    &__collection {
        margin-bottom: 15px;
        .product__title {
            font-size: 15px; }
        .product__details {
            color: $grey; } } }
