.slider {
    @include sm {
        margin-left: 10px; }
    @include invis;
    &:hover {
        .btn-slider {
            @include vis; } }
    &__slide {
        width: 700px !important;
        padding-bottom: 350px;
        background-color: $light-bg;
        @include cover;
        border-radius: $radiusBig;
        position: relative;
        @include md {
            width: 653px !important;
            padding-bottom: 326px; }
        @include sm {
            width: 285px !important;
            padding-bottom: 168px; } }
    .slick-slide {
        margin: 0 12px;
        @include sm {
            margin: 0 5px; } }
    .btn-slider {
        @include invis;
        &_prev {
            left: calc(50% - 480px); }
        &_next {
            right: calc(50% - 480px); } }
    &__text-first {
        font-size: 25px;
        line-height: 1.2;
        font-weight: 700;
        position: absolute;
        left: 40px;
        top: 40px;
        @include sm {
            left: 10px;
            top: 10px;
            font-size: 15px; } }
    &__text-second {
        position: absolute;
        top: 70px;
        right: 50px;
        @include sm {
            top: 15px;
            right: 15px; }
        h2 {
            font-size: 60px;
            font-weight: 700;
            line-height: 1;
            margin-bottom: 40px;
            @include sm {
                font-size: 20px;
                margin-bottom: 16px; } }
        p {
            font-size: 20px;
            line-height: 1.25;
            @include sm {
                font-size: 15px; } } }
    &__text-third {
        position: absolute;
        left: 40px;
        bottom: 40px;
        @include sm {
            left: 20px;
            bottom: 10px; }
        p {
            font-size: 20px;
            line-height: 1.25;
            @include sm {
                font-size: 15px; } }
        h2 {
            font-size: 50px;
            line-height: 1;
            @include sm {
                font-size: 24px; } } } }



.slick-arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%); }

.cards-slider {
    margin: 0 -14px 26px;
    display: flex;
    @include md {
        overflow: visible;
        margin-right: -50px; }
    @include sm {
        margin-bottom: 4px; }
    .card-slide {
        margin: 14px; }
    .slick-slide {
        margin: 0 12px;
        width: 300px !important;
        @include r(900) {
            width: 266px !important; }
        @include r(500) {
            width: 219px !important;
            margin: 0 5px; } } }

.slider-single {
    overflow: hidden;
    @include sm {
        overflow: visible;
        padding-left: 11px;
        .slick-list {
            overflow: visible; } }
    &__slide {
        padding-bottom: 630px;
        @include cover;
        border-radius: $radiusBig;
        @include md {
            padding-bottom: 374px; }
        @include sm {
            width: 270px;
            padding-bottom: 310px; } }
    .slick-slide {
        @include sm {
            margin: 0 6px; } }
    .btn-single-slider {
        &_prev {
            left: 20px;
            transition: visibility 0.1s;
            &.is-hidden {
                visibility: hidden; } }
        &_next {
            right: 20px;
            transition: visibility 0.1s;
            &.is-hidden {
                visibility: hidden; } } } }

