.preview {
	position: relative;
	display: block;
	&:hover {
		.preview__check {
			opacity: 0.7; } }
	input {
		position: absolute;
		left: 0;
		top: -1px;
		opacity: 0;
		width: 16px;
		height: 16px;
		&:checked {
			~ .preview__check {
				opacity: 1; }
			~ .preview__main {
				@include vis; } } }
	&__image {
		display: block;
		padding-bottom: calc(100% + 3px);
		border-radius: $radiusBig;
		@include cover; }
	&__check {
		// +flex-center
		position: absolute;
		top: 10px;
		right: 10px;
		// height: 35px
		// width: 35px
		// border-radius: 50%
		// background-color: #fff
		// font-size: 0
		opacity: 0;
		transition: opacity .3s;
		// .icon
		// 	font-size: 15px
 }		// 	color: $blue
	&__delete {
		position: absolute;
		top: 10px;
		right: 10px; }
	&__main {
		position: absolute;
		bottom: 10px;
		left: 50%;
		transform: translateX(-50%);
		@include invis; } }

.product {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	&__image {
		flex: 0 0 55px;
		border-radius: $radius;
		width: 55px;
		padding-bottom: 55px;
		@include cover; }
	&__main {
		flex: 0 0 calc(100% - 55px);
		padding-left: 16px;
		position: relative; }
	&__title,
	&__price {
		color: $black;
		font-weight: 500; }
	&__title {
		font-size: 13px; }
	&__descr {
		font-size: 13px;
		line-height: 1.23;
		color: $grey;
		margin-top: 14px; }
	&__close {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		right: 0;
		background-color: transparent;
		color: $light-grey;
		.icon {
			font-size: 12px; } } }


